"><h2 class="our-story fw-bold fs-1">Our Story</h2><p class="story-text m-0 mt-4 mb-5">Embracing the sublime pleasure of drink, the joy of good food and the comfort of family  Pourhouse is the embodiment of a passion for sharing the simple indulgences of life with friends. Just southeast of the steam clock in Gastown, Pourhouse harkens back to century-old styles of drinking and dining. The 100-year-old building was built in 1910, as home to the Leckie Boot Company  the original structure crafted with Douglas fir ceilings, supported by large wood pillars, and brick walls. Pourhouse is furnished in antiques of the era  complemented by custom creations. Every piece in Pourhouse has a story. The cornerstone is the 38 ft. bar  lovingly handcrafted from 120 year-old planks of reclaimed Douglas fir. Entrance and hallway walls are finished with era-inspired textures and gold leaf detailing  no aesthetic left unconsidered.</p><button class="btn btn-outline-secondary w-50 py-2" type="button">Read More</button></div><hr class="mt-3"><!-- Slider--><about-carousel :items="items"></about-carousel>