<div class="bd-callout bd-callout-info ps-5 text-black-50 fs-6"><h4 class="fw-bold"><fa-icon class="me-2" icon="fa-comment"></fa-icon>Restraurant Info:</h4><p class="m-0 mb-1 mt-3"><strong>Name:</strong> {{ locData.name }}</p><p class="m-0 mb-1"><strong>Phone:</strong> {{ locData.phone }}</p><p class="m-0"><strong>Address:</strong> {{ locData.address }}</p></div><div class="d-flex justify-content-center align-items-center gap-4 mt-5"><add-cat-btn :data="categoriesData" :locId="parseInt(locId)" :userId="userId"></add-cat-btn><button class="btn btn-outline-secondary" @click="redirectToHome()">Go back to restaruants list</button></div><div><p class="text-secondary fs-5 text-center mt-5">List of all categories ({{ categoriesData.length }})<delete-all-cats :data="categoriesData"></delete-all-cats></p><!-- If there are categories--><div class="overflow-x" v-if="categoriesData.length"><table class="
      table
      table-striped
      table-hover
      table-responsive
      table-bordered
      mb-4
      shadow-sm
    "><thead class="table-dark"><tr class="text-center"><th scope="col">#</th><th scope="col">Category Name</th><th scope="col">Actions</th></tr></thead><tbody class="align-middle"><tr v-for="(cat, i) in categoriesData" :key="i"><th scope="row"><div class="d-flex justify-content-center align-items-center">{{ i + 1 }}</div></th><td>{{  cat.name  }}</td><td><div class="d-flex justify-content-center align-items-center gap-3"><div><button class="btn btn-secondary px-4" @click="redirectToView(cat.id)">View</button></div><edit-cat-btn :index="i" :data="cat"></edit-cat-btn><delete-cat-btn :index="i" :data="cat"></delete-cat-btn></div></td></tr></tbody></table></div><!-- If not?--><div v-else><div class="alert alert-warning alert-dismissible fade show text-center fs-5" role="alert"><strong>No data to show yet !</strong><button class="btn-close" type="button" data-bs-dismiss="alert" aria-label="Close"></button></div></div></div>