<div><!-- Modal trigger button --><button class="btn btn-primary px-4" type="button" data-bs-toggle="modal" :data-bs-target="'#EditItemModal' + index">Edit</button><!-- Modal --><div class="modal fade" :id="'EditItemModal' + index" tabindex="-1" :aria-labelledby="'EditItemModal' + index" aria-hidden="true"><div class="modal-dialog modal-lg modal-dialog-scrollable"><div class="modal-content"><div class="modal-header"><h5 class="modal-title" :id="'EditItemModal' + index">Edit Category</h5><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body d-flex flex-column justify-content-center align-items-center"><h5 class="text-danger mb-4">Are you sure you wanna apply these changes?</h5><form action="#" @submit.prevent novalidate><h3 class="text-center mb-4">Item<span class="d-inline-block ms-2 fst-italic text-black-50 fs-5">'{{ itemName }}'</span></h3><div class="row mb-3 gap-3 gap-md-0"><div class="mb-3 mx-auto w-75"><div class="form-floating"><input class="form-control form-control-lg" v-model.trim="itemName" type="text" name="name" placeholder="Item Name" id="nameInputFloat"><label for="nameInputFloat">Item Name</label><div class="p-1 text-danger" v-if="v$.itemName.$error">{{ v$.itemName.$errors[0].$message }}</div></div></div><div class="mb-3 mx-auto w-75"><div class="
                    quantityCont
                    d-flex
                    flex-column
                    flex-md-row
                    justify-content-between
                    form-control
                    form-control-lg
                "><button class="btn btn-dark px-3" @click="{quantity: quantity == 0 ? 0 : quantity--}">-</button><input class="border-0 flex-fill my-3 my-md-0" v-model="quantity" type="text" name="quantity" id="quantityInput" @focus="focusInput()" @blur="blurInput()"><button class="btn btn-dark px-3" @click="quantity++">+</button></div><div class="p-1 text-danger" v-if="v$.quantity.$error">{{ v$.quantity.$errors[0].$message }}</div></div><div class="row mb-3 gap-3 gap-md-0"><div class="mb-3 mx-auto w-75"><div class="form-floating"><input class="form-control form-control-lg" v-model.trim="price" type="number" name="price" placeholder="Item Price" id="priceInputFloat"><label for="priceInputFloat">Price</label><div class="p-1 text-danger" v-if="v$.price.$error">{{ v$.price.$errors[0].$message }}</div></div></div></div><div class="mb-3 mx-auto w-75"><div class="form-floating"><textarea class="form-control form-control-lg" v-model.trim="description" name="description" placeholder="Description" id="descriptionInput"></textarea><label for="descriptionInput">Description</label><div class="p-1 text-danger" v-if="v$.description.$error">{{ v$.description.$errors[0].$message }}</div></div><hr class="hr"></div><div class="d-flex flex-column justify-content-center align-items-center gap-4"><button class="btn btn-secondary btn-lg px-md-5" type="btn" @click="resetAll()" :disabled="editStatus.success || editStatus.fail">Reset</button><main-alert :toggleSuccess="editStatus.success" :toggleFail="editStatus.fail" :text="
                    editStatus.success
                      ? 'Successfully updated this item!'
                      : 'Could not update this item, try again!'
                  "></main-alert><hr class="hr"></div></div></form></div><div class="modal-footer"><button class="btn btn-warning" type="button" @click="handleEdit()" :disabled="editStatus.success || editStatus.fail">Apply Changes</button><button class="btn btn-secondary" type="button" data-bs-dismiss="modal" :disabled="editStatus.success || editStatus.fail">Cancel</button></div></div></div></div></div>