<div class="bd-callout bd-callout-info ps-5 text-black-50 fs-6"><h4 class="fw-bold"><fa-icon class="me-2" icon="fa-comment"></fa-icon>Restraurant Info:</h4><p class="m-0 mb-1 mt-3"><strong>Name:</strong> {{ locData.name }}</p><p class="m-0 mb-1"><strong>Phone:</strong> {{ locData.phone }}</p><p class="m-0"><strong>Address:</strong> {{ locData.address }}</p></div><div class="alert alert-warning alert-dismissible fade show text-center fs-5" role="alert"><fa-icon class="me-2" icon="fa-triangle-exclamation"></fa-icon><strong>You are now viewing category:<p class="d-inline-block m-0 ms-2 text-black-50 fst-italic">{{ categoryData.name }}</p></strong><button class="btn-close" type="button" data-bs-dismiss="alert" aria-label="Close"></button></div><div class="d-flex justify-content-center align-items-center gap-4 mt-5"><add-item-btn :data="{
      'userId': userId,
      'locId': locId,
      'catId': categoryData.id,
      'catName': categoryData.name
    }"></add-item-btn><button class="btn btn-outline-secondary" type="button" @click="goBack()">Go Back</button></div><div class="
  d-flex
  justify-content-between
  align-items-center
  flex-column
  flex-sm-row
  gap-4
  gap-sm-0
  mt-4
"><p class="m-0 text-secondary fs-5">All Items ({{ itemsData.length }})</p><delete-all-items :data="{
    'userId': userId,
    'catId': categoryData.id,
    'items': itemsData
  }"></delete-all-items></div><div class="row row-cols-1 row-cols-sm-2 row-cols-md-3 row-cols-lg-4 row-cols-xl-5 g-4 my-4"><div class="col" v-for="(item, i) in itemsData" :key="i"><div class="card h-100"><h5 class="card-header py-3 text-center">{{ item.name }}</h5><div class="card-body"><h5 class="card-title m-0 mb-1 text-dark fw-bold fs-5">Description:</h5><p class="card-text">{{ item.description }}</p></div><ul class="list-group list-group-flush"><li class="list-group-item"><strong class="me-2">Price:</strong>${{ numberWithCommas(item.price) }}</li><li class="list-group-item"><strong class="me-2">Quantity:</strong>{{ numberWithCommas(item.quantity) }}</li></ul><div class="card-footer d-flex justify-content-between"><edit-item-btn :index="i" :data="item" :otherData="{
            'userId': userId,
            'locId': locId,
            'catId': categoryData.id
          }"></edit-item-btn><delete-item-btn :index="i" :data="item"></delete-item-btn></div></div></div></div><hr><div><p class="text-secondary fs-5 text-center mt-5">List of all items ({{ itemsData.length }})</p><!-- If there are categories--><div class="overflow-x" v-if="itemsData.length"><table class="
      table
      table-striped
      table-hover
      table-responsive
      table-bordered
      mb-5
      shadow-sm
    "><thead class="table-dark"><tr class="text-center"><th scope="col">#</th><th scope="col">Item Name</th><th scope="col">Quantity</th><th scope="col">Price</th><th scope="col">Description</th><th scope="col">Actions</th></tr></thead><tbody class="align-middle"><tr v-for="(item, i) in itemsData" :key="i"><th scope="row"><div class="d-flex justify-content-center align-items-center">{{ i + 1 }}</div></th><td>{{ item.name }}</td><td class="text-center">{{ numberWithCommas(item.quantity) }}</td><td class="text-center">${{ numberWithCommas(item.price) }}</td><td>{{ item.description }}</td><td><div class="d-flex justify-content-center align-items-center gap-3"><edit-item-btn :index="i" :data="item" :otherData="{
                  'userId': userId,
                  'locId': locId,
                  'catId': categoryData.id,
                }"></edit-item-btn><delete-item-btn :index="i" :data="item"></delete-item-btn></div></td></tr></tbody></table></div><!-- If not?--><div v-else><div class="alert alert-warning alert-dismissible fade show text-center fs-5" role="alert"><strong>No data to show yet !</strong><button class="btn-close" type="button" data-bs-dismiss="alert" aria-label="Close"></button></div></div></div>