<div><!-- Modal trigger button --><button class="btn btn-danger px-4" type="button" data-bs-toggle="modal" :data-bs-target="'#DeleteCatModal' + index">Delete</button><!-- Modal --><div class="modal fade" :id="'DeleteCatModal' + index" tabindex="-1" :aria-labelledby="'DeleteCatModal' + index" aria-hidden="true"><div class="modal-dialog modal-lg modal-dialog-scrollable"><div class="modal-content"><div class="modal-header"><h5 class="modal-title" :id="'DeleteCatModal' + index">Delete Category</h5><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body d-flex flex-column justify-content-center align-items-center"><h5 class="text-danger mb-4">Are you sure you wanna delete this category and its related items?</h5><div><p class="fs-4"><strong>Category name:</strong> {{ name }}</p></div><hr class="hr"><main-alert :toggleSuccess="editStatus.success" :toggleFail="editStatus.fail" :text="editStatus.success ? 'Successfully deleted!' : 'Failed to perform operation!'"></main-alert></div><div class="modal-footer"><button class="btn btn-danger" type="button" @click="handleDelete(data.id)" :disabled="editStatus.success || editStatus.fail">Yes, delete</button><button class="btn btn-secondary" type="button" data-bs-dismiss="modal" :disabled="editStatus.success || editStatus.fail">Cancel</button></div></div></div></div></div>