<div class="carousel slide carousel-fade pb-2 mt-5" id="slider-carousel" data-bs-interval="5000" data-bs-ride="carousel"><div class="carousel-indicators rounded gap-2"><button v-for="index in items.length" :key="index" type="button" data-bs-target="#slider-carousel" :data-bs-slide-to="index-1" :class="{ 'active': index === 1}" aria-current="true" :aria-label="`Slide ${index}`"></button></div><div class="carousel-inner"><div v-for="(item, index) in items" :class="{
        'carousel-item': true,
        'active': index === 1
      }"><div class="slide-img rounded-4 overflow-hidden mb-5 mx-auto"><img class="img-fluid" :src="require(`../../assets/${item.imgName}`)" alt="Image"></div><div class="carousel-caption d-none d-md-block rounded-bottom"><h5>{{ item.title }}</h5><p>{{ item.text }}</p></div></div></div><button class="carousel-control-prev" type="button" data-bs-target="#slider-carousel" data-bs-slide="prev"><span class="fs-1" aria-hidden="true"><fa-icon icon="fa-angle-left"></fa-icon></span><span class="visually-hidden">Previous</span></button><button class="carousel-control-next" type="button" data-bs-target="#slider-carousel" data-bs-slide="next"><span class="fs-1" aria-hidden="true"><fa-icon icon="fa-angle-right"></fa-icon></span><span class="visually-hidden">Next</span></button></div>