<div><!-- Button trigger modal --><button class="btn btn-outline-secondary pulseBtn" type="button" data-bs-toggle="modal" data-bs-target="#addCatModal">Add new category</button><!-- Modal --><div class="modal fade" id="addCatModal" tabindex="-1" aria-labelledby="addCategoryModal1" aria-hidden="true"><div class="modal-dialog modal-lg modal-dialog-scrollable"><div class="modal-content"><div class="modal-header"><h5 class="modal-title" id="addCategoryModal1">Add a new category</h5><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body d-flex flex-column justify-content-center align-items-center"><form action="#" @submit.prevent novalidate><h3 class="text-center mb-4">Category required data</h3><div class="row mb-3 gap-3 gap-md-0"><div class="mb-3 mx-auto w-75"><div class="form-floating"><input class="form-control form-control-lg" v-model.trim="name" type="text" name="name" placeholder="Category name" id="nameInputFloat"><label for="nameInputFloat">Category name</label><div class="p-1 text-danger" v-if="v$.name.$error">{{ v$.name.$errors[0].$message }}</div></div><hr class="hr"></div><div class="d-flex flex-column justify-content-center align-items-center gap-4"><button class="btn btn-secondary btn-lg px-md-5" type="btn" @click="resetAll()" :disabled="operationState.success || operationState.fail">Reset</button><main-alert :toggleSuccess="operationState.success" :toggleFail="operationState.fail" :text="operationState.success ? 'Added data successfully!' : 'Something went wrong, try again!'"></main-alert><main-alert :toggleFail="duplicated" text="This category name already exists!"></main-alert></div></div></form></div><div class="modal-footer"><button class="btn btn-primary" type="button" @click="handleEdit()" :disabled="operationState.success || operationState.fail">Add now</button><button class="btn btn-secondary" type="button" data-bs-dismiss="modal" :disabled="operationState.success || operationState.fail">Close</button></div></div></div></div></div>