<div><!-- Button trigger modal --><button class="btn btn-outline-secondary pulseBtn" type="button" data-bs-toggle="modal" data-bs-target="#addItemModal">Add new item</button><!-- Modal --><div class="modal fade" id="addItemModal" tabindex="-1" aria-labelledby="addItemModal1" aria-hidden="true"><div class="modal-dialog modal-lg modal-dialog-scrollable"><div class="modal-content"><div class="modal-header"><h5 class="modal-title" id="addItemModal1">Add a new item to<span class="d-inline-block mx-1 fst-italic">'{{ data.catName }}'</span> category</h5><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body"><form action="#" @submit.prevent novalidate><h3 class="text-center mb-4">Required item data</h3><div class="row mb-3 gap-3 gap-md-0"><div class="mb-3 mx-auto w-75"><div class="form-floating"><input class="form-control form-control-lg" v-model.trim="ItemName" type="text" name="name" placeholder="Item name" id="nameInputFloat"><label for="nameInputFloat">Item name</label><div class="p-1 text-danger" v-if="v$.ItemName.$error">{{ v$.ItemName.$errors[0].$message }}</div></div></div><div class="mb-3 mx-auto w-75"><div class=" quantityCont d-flex flex-column flex-md-row justify-content-between form-control form-control-lg"><button class="btn btn-dark px-3" @click="{quantity: quantity == 0 ? 0 : quantity--}">-</button><input class="border-0 flex-fill my-3 my-md-0" v-model="quantity" type="text" name="quantity" id="quantityInput" @focus="focusInput()" @blur="blurInput()"><button class="btn btn-dark px-3" @click="quantity++">+</button></div><div class="p-1 text-danger" v-if="v$.quantity.$error">{{ v$.quantity.$errors[0].$message }}</div></div><div class="mb-3 mx-auto w-75"><div class="form-floating"><input class="form-control form-control-lg" v-model.trim="price" type="number" name="price" placeholder="Item Price" id="priceInputFloat"><label for="priceInputFloat">Price</label><div class="p-1 text-danger" v-if="v$.price.$error">{{ v$.price.$errors[0].$message }}</div></div></div><div class="mb-3 mx-auto w-75"><div class="form-floating"><textarea class="form-control form-control-lg" v-model.trim="description" name="description" placeholder="Description" id="descriptionInput"></textarea><label for="descriptionInput">Description</label><div class="p-1 text-danger" v-if="v$.description.$error">{{ v$.description.$errors[0].$message }}</div></div><hr class="hr"></div><div class="d-flex flex-column justify-content-center align-items-center gap-4"><button class="btn btn-secondary btn-lg px-md-5" type="btn" @click="resetAll()" :disabled="operationState.success || operationState.fail">Reset</button><main-alert :toggleSuccess="operationState.success" :toggleFail="operationState.fail" :text="operationState.success ? 'Added data successfully!' : 'Something went wrong, try again!'"></main-alert><main-alert :toggleFail="duplicated" text="This item name already exists!"></main-alert></div></div></form></div><div class="modal-footer"><button class="btn btn-primary" type="button" @click="addItem()" :disabled="operationState.success || operationState.fail">Register this item</button><button class="btn btn-secondary" type="button" data-bs-dismiss="modal" :disabled="operationState.success || operationState.fail">Cancel</button></div></div></div></div></div>