<div><!-- Modal trigger button --><button class="btn btn-danger px-4" type="button" data-bs-toggle="modal" :data-bs-target="'#DeleteItemModal' + index">Delete</button><!-- Modal --><div class="modal fade" :id="'DeleteItemModal' + index" tabindex="-1" :aria-labelledby="'DeleteItemModal' + index" aria-hidden="true"><div class="modal-dialog modal-lg modal-dialog-scrollable"><div class="modal-content"><div class="modal-header"><h5 class="modal-title" :id="'DeleteItemModal' + index">Delete Item</h5><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body d-flex flex-column justify-content-center align-items-center"><h4 class="text-danger mb-4">Are you sure you wanna permanently delete this item?</h4><div><p class="fs-4 text-black-50"><strong class="text-black me-2">Item name:</strong>{{ data.name }}</p><p class="fs-4 text-black-50"><strong class="text-black me-2">Quantity:</strong>{{ data.quantity }}</p><p class="fs-4 text-black-50"><strong class="text-black me-2">Price:</strong>${{ data.price }}</p><p class="fs-4 text-black-50"><strong class="text-black me-2">Description:</strong>{{ data.description }}</p></div><hr class="hr"><main-alert :toggleSuccess="editStatus.success" :toggleFail="editStatus.fail" :text="editStatus.success ? 'Successfully deleted!' : 'Failed to delete item!'"></main-alert></div><div class="modal-footer"><button class="btn btn-danger" type="button" @click="handleDelete()" :disabled="editStatus.success || editStatus.fail">Yes, delete</button><button class="btn btn-secondary" type="button" data-bs-dismiss="modal" :disabled="editStatus.success || editStatus.fail">Cancel</button></div></div></div></div></div>