<div><!-- Modal trigger button --><button class="btn btn-primary px-4" type="button" data-bs-toggle="modal" :data-bs-target="'#EditCatModal' + index">Edit</button><!-- Modal --><div class="modal fade" :id="'EditCatModal' + index" tabindex="-1" :aria-labelledby="'EditCatModal' + index" aria-hidden="true"><div class="modal-dialog modal-lg modal-dialog-scrollable"><div class="modal-content"><div class="modal-header"><h5 class="modal-title" :id="'EditCatModal' + index">Edit Category</h5><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body d-flex flex-column justify-content-center align-items-center"><h5 class="text-danger mb-4">Are you sure you wanna apply these changes?</h5><form action="#" @submit.prevent novalidate><h3 class="text-center mb-4">Category data</h3><div class="row mb-3 gap-3 gap-md-0"><div class="mb-3 mx-auto w-75"><div class="form-floating"><input class="form-control form-control-lg" v-model.trim="name" type="text" name="name" placeholder="Restaurant name" id="nameInputFloat"><label for="nameInputFloat">Category name</label></div><div class="p-1 text-danger" v-if="v$.name.$error">{{ v$.name.$errors[0].$message }}</div><hr class="hr"></div><div class="d-flex flex-column justify-content-center align-items-center gap-4 mt-5"><button class="btn btn-secondary btn-lg px-md-5" type="btn" @click="resetAll()" :disabled="editStatus.success || editStatus.fail">Reset</button><main-alert :toggleSuccess="editStatus.success" :toggleFail="editStatus.fail" :text="editStatus.success ? 'Operation done!' : 'Failed to perform operation!'"></main-alert></div></div></form></div><div class="modal-footer"><button class="btn btn-warning" type="button" @click="handleEdit(data.id)" :disabled="editStatus.success || editStatus.fail">Apply Changes</button><button class="btn btn-secondary" type="button" data-bs-dismiss="modal" :disabled="editStatus.success || editStatus.fail">Cancel</button></div></div></div></div></div>