<div class="profile d-flex flex-column justify-content-center align-items-center w-75 m-auto"><h3 class="text-center fw-bold fs-1 mb-4"><fa-icon class="me-3" icon="file-pen"></fa-icon>Edit Profile</h3><form action="#" @submit.prevent novalidate><div class="row mb-3 gap-3 gap-md-0"><div class="mb-3 mx-auto w-75"><div class="form-floating"><input class="form-control form-control-lg" v-model.trim="username" type="text" name="username" placeholder="Username" id="usernameInputFloat"><label for="usernameInputFloat">Username</label></div><div class="p-1 text-danger" v-if="v$.username.$error">{{  v$.username.$errors[0].$message  }}</div></div><div class="mx-auto w-75"><div class="mb-3"><div class="input-group"><div class="form-floating"><input class="form-control form-control-lg" v-model.trim="password" :type="typePass" name="password" placeholder="Password" id="passwordInputFloat"><label for="passwordInputFloat">Password</label></div><button class="input-group-text" @click="togglePass"><fa-icon :icon="showPass ? 'fa-solid fa-eye' : 'fa-solid fa-eye-slash'"></fa-icon></button></div><div class="p-1 text-danger" v-if="v$.password.$error">{{  v$.password.$errors[0].$message  }}</div></div></div><div class="mx-auto w-75"><div class="form-floating"><input class="form-control form-control-lg" v-model="email" type="email" name="email" placeholder="E-Mail" id="emailInputFloat" disabled><label for="emailInputFloat">E-Mail</label></div></div><div class="d-flex justify-content-center gap-4 mt-5"><button class="btn btn-secondary btn-lg px-md-5" type="btn" @click="resetAll()">Reset</button><button class="btn btn-secondary btn-lg px-md-5" type="btn" @click="redirect('home', 1200)">Cancel</button></div><div class="d-flex justify-content-center mt-3"><button class="btn btn-secondary btn-lg w-50" type="submit" @click="updateProfile()">Submit</button></div></div><main-alert :toggleSuccess="updateStatus" :toggleFail="updateStatusErr" :text="updateStatus ? 'Profile is successfully updated!' : 'Something went wrong, try again!'"></main-alert></form></div><main-loader v-if="showLoader"></main-loader>